import React, { Component } from 'react';

class NotCurrentlyMessaging extends Component {
  render() {
    return(
      <div className="select-a-buddy">
        <div className="select-a-buddy-text">Select a buddy to message!</div>
      </div>
    )
  }
}

export default NotCurrentlyMessaging;



